
import Vue from "vue";
const FIRST_NAME = "Frank";
const LAST_NAME = "Liu";
export default Vue.extend({
  name: "Carousel",
  components: {},
  data: () => ({
    FIRST_NAME,
    LAST_NAME,
  }),
  methods: {
    firstName(index: number): any {
      return {
        "animation-delay": index * 0.2 + 0.8 + "s",
      };
    },
    lastName(index: number): any {
      return {
        "animation-delay": (index + FIRST_NAME.length) * 0.2 + 0.8 + "s",
      };
    },
  },
});
